import React from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { Button, Flex, Heading, IconButton, AddIcon, MinusIcon, useModal, Text } from '@robustswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import useStake from 'hooks/useStake'
import useUnstake from 'hooks/useUnstake'
import { getBalanceNumber } from 'utils/formatBalance'
import DepositModal from '../DepositModal'
import WithdrawModal from '../WithdrawModal'

interface FarmCardActionsProps {
  stakedBalance?: BigNumber
  tokenBalance?: BigNumber
  tokenName?: string
  pid?: number
  depositFeeBP?: number
  stakedTokenPriceUSD?: BigNumber
}

const IconButtonWrapper = styled.div`
  display: flex;
  svg {
    width: 20px;
  }
`

const StakeAction: React.FC<FarmCardActionsProps> = ({
  stakedBalance,
  tokenBalance,
  tokenName,
  pid,
  depositFeeBP,
  stakedTokenPriceUSD,
}) => {
  const TranslateString = useI18n()
  const { onStake } = useStake(pid)
  const { onUnstake } = useUnstake(pid)

  const rawStakedBalance = getBalanceNumber(stakedBalance)
  const stakedBalanceUSD = getBalanceNumber(stakedBalance.times(stakedTokenPriceUSD))
  const displayBalance = rawStakedBalance.toLocaleString()
  const tokenMode = tokenName.includes('-')

  const [onPresentDeposit] = useModal(
    <DepositModal max={tokenBalance} onConfirm={onStake} tokenName={tokenName} depositFeeBP={depositFeeBP} />,
  )
  const [onPresentWithdraw] = useModal(
    <WithdrawModal max={stakedBalance} onConfirm={onUnstake} tokenName={tokenName} />,
  )

  const renderStakingButtons = () => {
    return rawStakedBalance === 0 ? (
      <Button variant="subtle" onClick={onPresentDeposit} style={{ minWidth: '122px' }}>
        {TranslateString(999, 'STAKE')}
      </Button>
    ) : (
      <IconButtonWrapper>
        <IconButton variant="tertiary" onClick={onPresentWithdraw} mr="6px">
          <MinusIcon color="primary" />
        </IconButton>
        <IconButton variant="tertiary" onClick={onPresentDeposit}>
          <AddIcon color="primary" />
        </IconButton>
      </IconButtonWrapper>
    )
  }

  return (
    <Flex justifyContent="space-between" alignItems="center">
      {renderStakingButtons()}
      <div>
        {/* <Heading color={rawStakedBalance === 0 ? 'textDisabled' : 'text'} style={{ marginRight: 20 }}>
          <Text fontSize="12px" style={{ color: '#A5A5A5', marginBottom: 5 }}>Staked {tokenName.includes('-') ? 'LP' : 'Tokens'}</Text>
          {displayBalance}
        </Heading> */}
        <div style={{ textAlign: 'left', paddingLeft: 16 }}>
          <Text fontSize="12px" style={{ color: '#A5A5A5' }}>
            {!tokenMode ? TranslateString(333, 'Staked Tokens') : TranslateString(999, 'Staked LP')}
          </Text>
          <Text fontSize="16px">{stakedBalance.toString()} {!tokenMode ? tokenName : 'LP'}</Text>
          <Text fontSize="12px">${(stakedTokenPriceUSD.toNumber() * stakedBalance.toNumber()).toLocaleString()}</Text>
        </div>
        {rawStakedBalance !== 0 && (
          <Text fontSize="13px" mt="4px" color="primary" style={{ textAlign: 'left' }}>
            ~${stakedBalanceUSD.toLocaleString()}
          </Text>
        )}
      </div>
    </Flex >
  )
}

export default StakeAction
