import React, { useMemo, useState, useCallback } from 'react'
import BigNumber from 'bignumber.js'
import styled, { keyframes } from 'styled-components'
import { provider } from 'web3-core'
import { getContract } from 'utils/erc20'
import { getFullDisplayBalance, getBalanceNumber } from 'utils/formatBalance'
import { Button, Flex, Heading, Text, LinkExternal, IconButton, AlarmIcon, useModal } from '@robustswap-libs/uikit'
import { Farm } from 'state/types'
import { useFarmFromPid, useFarmUser } from 'state/hooks'
import useI18n from 'hooks/useI18n'
import UnlockButton from 'components/UnlockButton'
import { useApprove } from 'hooks/useApprove'
import StakeAction from './StakeAction'
import HarvestAction from './HarvestAction'
import HarvestCountdownModal from '../HarvestCountdownModal'

const Action = styled.div`
  padding-top: 4px;
`

const StyledIconButton = styled(IconButton)`
  height: 16px;
  margin-left: auto;

  svg {
    width: 18px;
    fill: ${({ theme }) => theme.colors.warning};
  }
`

const tada = keyframes`
  from {
    transform: scale3d(1, 1, 1);
  }

  10%,
  20% {
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }

  30%,
  50%,
  70%,
  90% {
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
  }

  40%,
  60%,
  80% {
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
`

const TadaAlarmIcon = styled(AlarmIcon)`
  animation: ${tada} 5s ease-in-out infinite;
  transform: translate3d(0, 0, 0);
`

const StyledLinkExternal = styled(LinkExternal)`
  svg {
    height: 20px;
    width: 20px;
  }
`

export interface FarmWithStakedValue extends Farm {
  apy?: BigNumber
}

interface FarmCardActionsProps {
  farm: FarmWithStakedValue
  ethereum?: provider
  account?: string
  harvestInterval?: number
  stakedTokenPriceUSD?: BigNumber
  cakePrice?: BigNumber
  tokenMode: boolean
  addLiquidityUrl?: string
}

const CardActions: React.FC<FarmCardActionsProps> = ({
  farm,
  ethereum,
  account,
  harvestInterval,
  stakedTokenPriceUSD,
  cakePrice,
  tokenMode,
  addLiquidityUrl
}) => {
  const TranslateString = useI18n()
  const [requestedApproval, setRequestedApproval] = useState(false)
  const { pid, lpAddresses, tokenAddresses, isTokenOnly, depositFeeBP } = useFarmFromPid(farm.pid)
  const { allowance, tokenBalance, stakedBalance, nextHarvestUntil, canHarvest, earnings } = useFarmUser(pid)
  const rawStakedBalance = getBalanceNumber(stakedBalance)
  const displayBalance = rawStakedBalance.toLocaleString()
  const lpAddress = lpAddresses[process.env.REACT_APP_CHAIN_ID]
  const tokenAddress = tokenAddresses[process.env.REACT_APP_CHAIN_ID]
  const lpName = farm.lpSymbol.toUpperCase()
  const isApproved = account && allowance && allowance.isGreaterThan(0)

  const lpContract = useMemo(() => {
    if (isTokenOnly) {
      return getContract(ethereum as provider, tokenAddress)
    }
    return getContract(ethereum as provider, lpAddress)
  }, [ethereum, lpAddress, tokenAddress, isTokenOnly])

  const { onApprove } = useApprove(lpContract)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const renderApprovalOrStakeButton = () => {
    return isApproved ? (tokenBalance.toNumber() === 0 && tokenMode) ? (
      <Flex style={{ justifyContent: 'space-between', alignItems: 'center' }}>
        <Button variant="subtle" as="a" href={addLiquidityUrl} style={{ width: '100%' }}>
          {TranslateString(999, 'GET LP')}
        </Button>
      </Flex>
    ) : (
      <StakeAction
        stakedBalance={stakedBalance}
        tokenBalance={tokenBalance}
        tokenName={lpName}
        pid={pid}
        depositFeeBP={depositFeeBP}
        stakedTokenPriceUSD={stakedTokenPriceUSD}
      />
    ) : (
      <Flex style={{ justifyContent: 'space-between', alignItems: 'center' }}>
        <Button variant="subtle" disabled={requestedApproval} onClick={handleApprove} style={{ width: '100%' }}>
          {TranslateString(999, 'ENABLE')}
        </Button>
      </Flex>
    )
  }

  const [onPresentHarvestCountdown] = useModal(
    <HarvestCountdownModal nextHarvestUntil={nextHarvestUntil} harvestInterval={harvestInterval} farm={farm} />,
  )

  return (
    <Action>
      <Flex>
        <Text textTransform="uppercase" fontSize="10px" style={{ color: '#A5A5A5' }}>
          {TranslateString(331, 'Earned')}
        </Text>
        <Text textTransform="uppercase" fontSize="10px" pr="3px" style={{ color: '#A5A5A5' }}>
          {/* TODO: Is there a way to get a dynamic value here from useFarmFromSymbol? */}
          &nbsp;RBS
        </Text>
        {(stakedBalance.gt(0) || earnings.gt(0)) && nextHarvestUntil > 0 && !canHarvest ? (
          <StyledIconButton onClick={onPresentHarvestCountdown} variant="text" size="sm">
            <TadaAlarmIcon />
          </StyledIconButton>
        ) : (
          ''
        )}
      </Flex>
      <HarvestAction earnings={earnings} pid={pid} canHarvest={canHarvest} cakePrice={cakePrice} cardMode={1} />
      {tokenMode ? <>
        <Flex>
          <Text fontSize="12px" style={{ color: '#A5A5A5' }}>
            {tokenBalance.toNumber() <= 0 ? TranslateString(453, 'Available LP') : TranslateString(999, 'Staked LP')}
          </Text>
        </Flex>
        <Flex justifyContent="space-between">
          {tokenBalance.toNumber() <= 0 ?
            <div style={{ textAlign: 'left' }}>
              <Text fontSize="16px">
                {getFullDisplayBalance(tokenBalance)} LP
              </Text>
              <Text fontSize="12px">${Number(getFullDisplayBalance(tokenBalance)) * stakedTokenPriceUSD.toNumber()}</Text>
            </div> :
            <Heading style={{ marginTop: 15, opacity: 0 }} />
          }
          {!account ? <UnlockButton /> : renderApprovalOrStakeButton()}
        </Flex>
      </> :
        <>
          <Flex>
            <Text fontSize="12px" style={{ color: '#A5A5A5', marginBottom: 8 }}>
              {TranslateString(329, 'Staked')} Tokens
            </Text>
          </Flex>
          {!account ? <UnlockButton fullWidth /> : renderApprovalOrStakeButton()}
        </>
      }
    </Action>
  )
}

export default CardActions
