import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import { Button, Flex, Heading, Text } from '@robustswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import { useHarvest } from 'hooks/useHarvest'
import { getBalanceNumber } from 'utils/formatBalance'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import useStake from '../../../../hooks/useStake'

interface FarmCardActionsProps {
  earnings?: BigNumber
  pid?: number
  canHarvest: boolean
  cakePrice?: BigNumber
  cardMode?: number
}

const BalanceAndCompound = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
`

const HarvestAction: React.FC<FarmCardActionsProps> = ({ earnings, pid, canHarvest, cakePrice, cardMode = 0 }) => {
  const TranslateString = useI18n()
  const [pendingTx, setPendingTx] = useState(false)
  const { onReward } = useHarvest(pid)
  // const { account } = useWallet()
  const { account } = useWallet()
  const rawEarningsBalance = getBalanceNumber(earnings)
  const earningBalanceUSD = getBalanceNumber(earnings.times(cakePrice))
  const displayBalance = rawEarningsBalance.toLocaleString()

  return (
    <Flex mb="8px" justifyContent="space-between" alignItems="center">
      {cardMode === 1 && <div>
        <Heading color={account ? rawEarningsBalance === 0 ? 'textDisabled' : 'text' : 'text'}>{account ? displayBalance : '-'}</Heading>
        {rawEarningsBalance !== 0 && (
          <Text fontSize="13px" mt="4px" color="primary" style={{ textAlign: 'left' }}>
            ~${earningBalanceUSD.toLocaleString()}
          </Text>
        )}
      </div>}
      <BalanceAndCompound style={{ width: cardMode ? 'auto' : '100%' }}>
        <Button
          disabled={rawEarningsBalance === 0 || !canHarvest || pendingTx}
          onClick={async () => {
            setPendingTx(true)
            await onReward()
            setPendingTx(false)
          }}
          style={{ minWidth: '122px', opacity: '0.4', color: 'white', width: '100%' }}
          variant="subtle"
        >
          {TranslateString(999, 'HARVEST')}
        </Button>
      </BalanceAndCompound>
    </Flex>
  )
}

export default HarvestAction
